import { showToast } from 'vant';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default {
  props: ['title', 'edit'],
  setup() {
    const store = useStore();
    let router = useRouter();
    const toBack = () => {
      router.back();
    };
    const EditClick = () => {
      if (store.state.cartList.length) {
        store.commit('isShowEdit');
        store.commit('edit');
      } else {
        showToast('空空如也');
      }
    };
    return {
      toBack,
      EditClick,
      store
    };
  }
};